/* MyPortfolio.css */
.my-portfolio {
    padding: 40px;
    background-color: #f9f9f9;
  }
  
  .portfolio-content {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
  }
  
  .my-portfolio h2 {
    font-size: 2rem;
    color: #333;
  }
  
  .portfolio-item {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    background-color: #fff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  }
  
  .portfolio-item h3 {
    font-size: 1.5rem;
    color: #333;
  }
  
  .portfolio-item p {
    font-size: 1.2rem;
    color: #555;
    line-height: 1.6;
  }
  
  .portfolio-item a {
    display: inline-block;
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #0074d9;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
  }
  
  .portfolio-item a:hover {
    background-color: #0056b3;
  }
  