/* AboutMe.css */
.about-me {
    padding: 40px;
    background-color: #f5f5f5;
  }
  
  .about-me-content {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
  }
  
  .about-me h2 {
    font-size: 2rem;
    color: #333;
  }
  
  .about-me p {
    font-size: 1.2rem;
    color: #555;
    line-height: 1.6;
  }
  
